import * as React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import Seo from "../components/seo"

const DairyPage = ({ data }) => {
  const posts = data.allMarkdownRemark.nodes

  return (
    <Layout>
      <div className="bg-white dark:bg-gray-900 shadow rounded-lg p-6 transition-colors">
        <h1 className="text-3xl font-bold text-gray-900 dark:text-white mb-6">Dairy</h1>
        <div className="space-y-8">
          {posts.map((post) => (
            <article key={post.id} className="prose prose-lg dark:prose-invert">
              <div className="text-sm text-gray-500 dark:text-gray-400 mb-2">
                {post.frontmatter.date}
              </div>
              <h2 className="text-2xl font-bold text-gray-900 dark:text-white mb-4">
                {post.frontmatter.title}
              </h2>
              <div
                dangerouslySetInnerHTML={{ __html: post.html }}
                className="text-gray-700 dark:text-gray-300"
              />
            </article>
          ))}
        </div>
      </div>
    </Layout>
  )
}

export const query = graphql`
  query {
    allMarkdownRemark(
      filter: { fileAbsolutePath: { regex: "/dairy/" } }
      sort: { frontmatter: { date: DESC } }
    ) {
      nodes {
        id
        html
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          title
        }
      }
    }
  }
`

export const Head = () => <Seo title="Dairy" />

export default DairyPage